<style>
#app {
  width: 100%;
  height: 100%;
}

.main {
  padding: 2rem;
  margin-left: calc(var(--side-bar));
}

.side-bar {
  position: fixed;
  width: var(--side-bar);
  height: 100vh;
  transform: translateX(calc(var(--side-bar) * -1));
  padding: 3rem 0.5rem;
  background-color: hsl(var(--accent-color));
  transition: all 0.2s ease;
}

.vis-slide-bar {
  transform: translateX(0);
}

.side-bar .item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.9rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
  border-radius: 50%;
}

.icon-menu {
  width: 2rem;
  height: 2rem;
}

a {
  display: block;
  text-decoration: none;
  color: currentColor;
}

.icon-menu svg {
  width: 100%;
  height: 100%;
  fill: hsl(var(--text-color));
}

.side-bar .item:hover {
  background-color: hsl(var(--main-color));
}

.side-bar .item.active {
  background-color: hsl(var(--main-color));
}

.event-message {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  width: 3rem;
  height: 3rem;
}

.event-message svg {
  width: 100%;
  height: 100%;
  fill: rgb(var(--text-color));
}
</style>
