<template>
  <div class>
    <modal name="modal-helper" height="auto" :scrollable="true" :pivotY="0.2" :maxWidth="700">
      <div class="modal-helper">
				<p>Кнопка "СОЗДАТЬ PLIST" создает только страницы не трогая категории</p>
				<br>
        <p>Добавление категории для страниц группой</p>
        <ol>
          <li>Поставить курсор в ячейку с категорией (в ней должно быть значение, которое скопируется в другие ячейки)</li>
          <li>Зажать SHIFT</li>
          <li>Поставить курсор в ячейку (вниз) до которой нужно копировать</li>
        </ol>
      </div>
    </modal>

    <header class="main-head">
      <div class="wrapper">
        <div class="inner">
          <button @click="backFolders" type="button" class="button btn-back">Назад</button>
          <div class="button-group">
            <button @click="createPlist" type="button" class="button">Создать PLIST</button>
            <button @click="savePlist" type="button" class="button">Сохранить PLIST</button>
          </div>
        </div>
        <div class="right-side">
          <button @click="showModalHelper" type="button" class="button help">?</button>
          <h4>{{ folderName }}</h4>
        </div>
      </div>
    </header>
    <section>
      <h2 class="title">Категории</h2>

      <div class="container categories">
        <draggable
          v-model="plist.categories"
          group="arrCategories"
          v-bind="dragOptions"
          @start="drag=true"
          @end="drag=false"
          handle=".categories-handle"
        >
          <transition-group type="transition" :name="!drag ? 'flip-list' : null">
            <div class="item" v-for="(categorie, i) of plist.categories" :key="i">
              <p class="categories-num">{{ i }}</p>
              <input
                type="text"
                class="categories-name"
                v-model="categorie.name"
                placeholder="название"
              />
              <input
                type="text"
                class="categories-color-text"
                v-model="categorie.textColor"
                placeholder="цвет текста"
              />
              <input
                type="text"
                class="categories-background-color"
                v-model="categorie.backgroundColor"
                placeholder="цвет заливки фона"
              />
              <input
                type="text"
                class="categories-selected-text-color"
                v-model="categorie.selectedTextColor"
                placeholder="ц т (выделеный)"
              />
              <input
                type="text"
                class="categories-selected-background-color"
                v-model="categorie.selectedBackgroundColor"
                placeholder="ц з ф (выделеный)"
              />
              <button class="dell-categories button alert" @click="dellCategories(i)">&#8722;</button>
              <button class="button" @click="addCategories(i)">+</button>
              <div class="drag-handle categories-handle">&equiv;</div>
            </div>
          </transition-group>
        </draggable>

        <!-- <button type="button" class="small button" @click="addCategories">Добавить категорию</button> -->
        <!-- <div class="item" v-for="(categorie, i) in plist.categories" :key="i">{{ categorie.name }}</div> -->
      </div>

      <h2 class="title">Страницы</h2>
      <div class="container pages">
        <draggable
          v-model="plist.pages"
          group="pages"
          v-bind="dragOptions"
          @start="drag=true"
          @end="drag=false"
          handle=".plist-pages"
        >
          <transition-group type="transition" :name="!drag ? 'flip-list' : null">
            <div class="item" v-for="(page, i) of plist.pages" :key="i">
              <input type="text" class="pages-name" v-model="page.archive" />
              <input
                @focus="pageFocus(i)"
                type="text"
                class="pages-categorie"
                v-model="page.categorie"
                placeholder="категория"
              />
              <button class="dell-pages button alert" @click="dellPage(i)">&#8722;</button>
              <button class="button" @click="addPage(i)">+</button>
              <div class="drag-handle plist-pages">&equiv;</div>
            </div>
          </transition-group>
        </draggable>

        <!-- <div class="item" v-for="(page, i) in plist.pages" :key="i">{{ page.archive }}</div> -->
      </div>

      <h2 class="title">Версия</h2>
      <div class="container version">
        <input type="text" v-model="plist.version" />
        <button class="button" @click="increaseVersion">Увеличить</button>
      </div>
    </section>
  </div>
</template>

 <script>
/* eslint-disable */
import draggable from 'vuedraggable';
import { mapState } from 'vuex';

class Categories {
  constructor() {
    this.name = '';
    this.textColor = '';
    this.backgroundColor = '';
    this.selectedTextColor = '';
    this.selectedBackgroundColor = '';
  }
}
class Pages {
  constructor() {
    this.archive = '';
    this.categorie = 0;
  }
}

export default {
  name: 'Plist',
  components: {
    draggable
  },
  data: function() {
    return {
      startInput: null,
      endInput: null,
      drag: false,
      plist: {},
      isSelect: false
    };
  },
  computed: {
    ...mapState({
      folderName: state => state.folderName,
      arrFoldersName: state => state.arrFoldersName,
      baseURL: state => state.baseURL
    }),
    dragOptions() {
      return {
        animation: 200,
        group: 'pages',
        disabled: false,
        ghostClass: 'ghost'
      };
    }
  },
  watch: {},
  methods: {
    async createPlist() {
      let response, result;
      let self = this;
      try {
        response = await fetch(this.baseURL + 'create_plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({
            arrFoldersName: self.arrFoldersName,
            name: self.folderName
          })
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }
      this.plist = result;
    },
    showModalHelper() {
      this.$modal.show('modal-helper');
    },
    hideModalHelper() {
      this.$modal.hide('modal-helper');
    },
    increaseVersion() {
      this.plist.version = (+this.plist.version + 0.1).toFixed(1);
    },
    pageFocus(index) {
      if (!this.startInput) {
        this.startInput = index;
      }

      if (this.isSelect) {
        this.endInput = index;
        this.autoFillCategorie();
      }
    },
    autoFillCategorie() {
      let firstItem = this.plist.pages[this.startInput].categorie;
      for (let i = this.startInput; i <= this.endInput; i++) {
        this.plist.pages[i].categorie = firstItem;
      }
      this.startInput = null;
    },
    addPage(index) {
      // this.plist.pages.push(new Pages());
      this.plist.pages.splice(index + 1, 0, new Pages());
    },
    dellPage(index) {
      this.plist.pages.splice(index, 1);
    },
    addCategories(index) {
      this.plist.categories.splice(index + 1, 0, new Categories());
      // this.plist.categories.push(new Categories());
    },
    dellCategories(index) {
      this.plist.categories.splice(index, 1);
    },
    backFolders() {
      this.$router.push('/');
    },
    async savePlist() {
      let response, result;
      let self = this;
      try {
        response = await fetch(this.baseURL + 'save_plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({ path: self.folderName, obj: self.plist })
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }
      this.plist = result;
      this.$store.dispatch('setEventMessage', 'save');
    },
    async getPlist() {
      let response, result;
      let self = this;
      try {
        response = await fetch(this.baseURL + 'plist', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({ name: self.folderName })
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }

      this.plist = result;
    },
    keyDownHelpers(event) {
      if (event.code === 'ShiftLeft') {
        this.isSelect = true;
      }
    },
    keyUpHelpers(event) {
      if (event.code === 'ShiftLeft') {
        this.isSelect = false;
      }
    },
    keyDestroy() {
      document.removeEventListener('keydown', this.keyDownHelpers);
      document.removeEventListener('keyup', this.keyUpHelpers);
    }
  },
  mounted() {
    document.addEventListener('keydown', this.keyDownHelpers);
    document.addEventListener('keyup', this.keyUpHelpers);
  },
  created() {
    this.getPlist();
  },
  beforeDestroy() {
    this.keyDestroy();
  }
};
</script>

 <style scoped>
.container {
  margin-bottom: 2rem;
}

.title {
  font-size: 170%;
  font-weight: 400;
}

.categories-num {
  display: flex;
  align-items: center;
}

input,
button {
  margin-bottom: 0;
}

.drag-handle {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid hsl(var(--accent-color));
}

.pages .item {
  display: grid;
  grid-template-columns: 3fr 1fr 2.5rem 2.5rem 2.5rem;
  grid-column-gap: 0.5rem;
  margin-bottom: 0.3rem;
  padding: 0.5rem;
  background-color: #fff;
  border-radius: 0.2rem;
}

.categories .item {
  display: grid;
  grid-template-columns: 1.5rem 3fr 1fr 1fr 1fr 1fr 2.5rem 2.5rem 2.5rem;
  grid-column-gap: 0.5rem;
  margin-bottom: 0.3rem;
  padding: 0.5rem;
  background-color: #fff;
  border-radius: 0.2rem;
}

.version {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0.5rem;
}

/* drag */
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
/* drag END */
</style>