<template>
  <div class>
    <div class="folders">
      <router-link
        tag="button"
        @click.native="setFileName(file)"
        v-for="(file, i) of arrFolderName"
        :key="i"
        :to="`/screenshot`"
        class="item button"
        exact
      >{{ file }}</router-link>
    </div>
  </div>
</template>

 <script>
/* eslint-disable */
import { mapState } from 'vuex';

export default {
  name: 'Folders',
  data: function() {
    return {
      arrFolderName: []
    };
  },
  computed: {
    ...mapState({
      baseURL: state => state.baseURL
    }),
    folderName: {
      get() {
        return this.$store.state.folderName;
      },
      set(value) {
        this.$store.commit('setFolderName', value);
      }
    }
  },
  watch: {},
  methods: {
    async getFolders() {
      try {
        let response = await fetch(this.baseURL);
        this.arrFolderName = await response.json();
      } catch (error) {
        console.error(error);
      }
    },
    setFileName(name) {
      this.folderName = name;
    }
  },
  mounted() {},
  created() {
    this.getFolders();
  }
};
</script>

 <style scoped>
.folders {
  display: flex;
  flex-direction: column;
}
.folders .item {
  padding: 1rem;
  margin-bottom: 1rem;
  cursor: pointer;
}
</style>