/* eslint-disable */
import VueRouter from 'vue-router';

import Screenshot from './components/Screenshot.vue';
import Plist from './components/Plist.vue';
import Options from './components/Options.vue';
import Folders from './components/Folders.vue';
import HtmlToTxt from './components/HtmlToTxt.vue';
import Zip from './components/Zip.vue';

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: Folders
    },
    {
      path: '/screenshot',
      component: Screenshot
    },
    {
      path: '/plist',
      component: Plist
    },
    {
      path: '/htmltotxt',
      component: HtmlToTxt
    },
    {
      path: '/zip',
      component: Zip
    },
    {
      path: '/options',
      component: Options
    }
    /* {
      path: '/plist',
      component: Plist
    },
    {
      path: '/options',
      component: Options
    } */
  ],
  // mode: 'history'
});

export default router;
