/* eslint-disable */
import Vue from 'vue';
import Vuex from 'vuex';

import screenshots from './screenshots';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    eventMessage: '',
    // baseURL: 'http://localhost:3013/',
    baseURL:
      process.env.NODE_ENV === 'production'
        ? 'https://haabbaah.github.io/Magazine-builder-UI-dist:3012/'
        : 'http://127.0.0.1:3012/',
    // baseURL: 'http://192.168.0.174:3012/',
    folderName: '', // Папка с проектом (2020_01)
    arrFoldersName: [] // Папки с index.html (только название без index.html)
  },
  mutations: {
    setFolderName(state, payload) {
      state.folderName = payload;
    },
    setArrFoldersName(state, payload) {
      state.arrFoldersName = payload;
    },
    changeEventMessage(state, payload) {
      state.eventMessage = payload;
    }
  },
  actions: {
    setEventMessage(ctx, payload) {
      ctx.commit('changeEventMessage', payload);
      setTimeout(() => {
        ctx.commit('changeEventMessage', '<p></p>');
      }, 2000);
    }
    /*    async copyToClipboard({ state, dispatch }) {
      await navigator.clipboard.writeText(state.textHTML);
      console.log('Text copied');
    },
    async copyToClipboardDZImages({ state, dispatch }) {
      await navigator.clipboard.writeText(state.imageHTML);
      console.log('Text copied');
    },
    async copyToClipboardGen({ state, dispatch }) {
      await navigator.clipboard.writeText(state.genHTML);
      console.log('Text copied');
    } */
  },
  modules: {
    screenshots
  }
});
