<template>
  <div class>
    <header class="main-head">
      <div class="wrapper">
        <div class="inner">
          <button @click="backFolders" type="button" class="button btn-back">Назад</button>
          <button @click="doArhive" type="button" class="button">Архивировать</button>
        </div>
        <h4>{{ folderName }}</h4>
      </div>
    </header>
  </div>
</template>

 <script>
/* eslint-disable */
import { mapState } from 'vuex';
export default {
  name: 'Zip',
  data: function() {
    return {};
  },
  computed: {
    ...mapState({
      folderName: state => state.folderName,
      baseURL: state => state.baseURL
    })
  },
  watch: {},
  methods: {
    backFolders() {
      this.$router.push('/');
    },
    async doArhive() {
      let response, result;
      let self = this;
      try {
        response = await fetch(this.baseURL + 'create_zip', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({ path: self.folderName })
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }
      this.$store.dispatch('setEventMessage', 'save');
    }
  },
  mounted() {},
  created() {}
};
</script>

