export default {
  state: {
    isOnly: false,
    isPass: false,
    isScreens: false
  },
  mutations: {
    setIsOnly(state, payload) {
      state.isOnly = payload;
    },
    setIsPass(state, payload) {
      state.isPass = payload;
    },
    setIsScreens(state, payload) {
      state.isScreens = payload;
    }
  },
  actions: {},
  getters: {}
};
