<template>
  <header class>
    <modal name="modal-helper" height="auto" :scrollable="true" :pivotY="0.2" :maxWidth="700">
      <div class="modal-helper">
        <p>Кнопка "СОЗДАТЬ СКРИНШОТЫ" создает по умолчанию скриншоты для thumb.jpg</p>
        <br />
        <p>Переключатель "СОЗДАТЬ В SCREENS" создаст по мимо thumb.jpg еще и в папку screens</p>
      </div>
    </modal>

    <header class="main-head">
      <div class="wrapper">
        <div class="inner">
          <button @click="backFolders" type="button" class="button btn-back">Назад</button>
          <button @click="createScreenshots" type="button" class="button">Создать скриншоты</button>
        </div>

        <div class="right-side">
          <button @click="showModalHelper" type="button" class="button help">?</button>
          <h4>{{ folderName }}</h4>
        </div>
      </div>
    </header>

    <section class="screenshot">
      <div class="options">
        <div class="switch-wrapper">
          <div class="switch-item">
            <div class="switch small">
              <input class="switch-input" id="only" type="checkbox" name="only" v-model="isOnly" />
              <label class="switch-paddle" for="only">
                <span class="show-for-sr">Download Kittens</span>
              </label>
            </div>
          </div>
          <label class="switch-text" for="only">Только эти страницы</label>
        </div>
        <div class="switch-wrapper">
          <div class="switch-item">
            <div class="switch small">
              <input class="switch-input" id="pass" type="checkbox" name="pass" v-model="isPass" />
              <label class="switch-paddle" for="pass">
                <span class="show-for-sr">Download Kittens</span>
              </label>
            </div>
          </div>
          <label class="switch-text" for="pass">Пропустить эти страницы</label>
        </div>
        <div class="switch-wrapper">
          <div class="switch-item">
            <div class="switch small">
              <input
                class="switch-input"
                id="screens"
                type="checkbox"
                name="screens"
                v-model="isScreens"
              />
              <label class="switch-paddle" for="screens">
                <span class="show-for-sr">Download Kittens</span>
              </label>
            </div>
          </div>
          <label class="switch-text" for="screens">Создать в screens</label>
        </div>
      </div>
      <div class="autoFill">
        <div class="checkbox" v-if="isOnly || isPass">
          <input type="checkbox" class="pass-checkbox" id="pass-checkbox" v-model="isAutoFill" />
          <label for="pass-checkbox"></label>
        </div>

        <!-- <input type="checkbox" class="pass-checkbox" v-if="isOnly || isPass" v-model="isAutoFill" /> -->
      </div>

      <div class="folder-wrapper">
        <div class="item" v-for="(foldersName, i) in arrFoldersName" :key="i">
          <p>{{ foldersName.name }}</p>
          <div class="inner">
            <div class="checkbox" v-if="isOnly" @click="clickCheckBox(i)">
              <input type="checkbox" :id="i" v-model="foldersName.isOnly" />
              <label :for="i"></label>
            </div>

            <div class="checkbox" v-if="isPass" @click="clickCheckBox(i)">
              <input type="checkbox" :id="i" v-model="foldersName.isPass" />
              <label :for="i"></label>
            </div>
          </div>
        </div>
      </div>
    </section>
  </header>
</template>

 <script>
/* eslint-disable */
import { mapState } from 'vuex';

export default {
  name: 'Screenshot',
  data: function() {
    return {
      isAutoFill: false
    };
  },
  computed: {
    ...mapState({
      folderName: state => state.folderName,
      baseURL: state => state.baseURL
    }),
    arrFoldersName: {
      get() {
        return this.$store.state.arrFoldersName;
      },
      set(value) {
        this.$store.commit('setArrFoldersName', value);
      }
    },
    isOnly: {
      get() {
        return this.$store.state.screenshots.isOnly;
      },
      set(value) {
        this.$store.commit('setIsOnly', value);
      }
    },
    isPass: {
      get() {
        return this.$store.state.screenshots.isPass;
      },
      set(value) {
        this.$store.commit('setIsPass', value);
      }
    },
    isScreens: {
      get() {
        return this.$store.state.screenshots.isScreens;
      },
      set(value) {
        this.$store.commit('setIsScreens', value);
      }
    }
  },
  watch: {
    isAutoFill(value) {
      for (const iterator of this.arrFoldersName) {
        if (this.isOnly) {
          iterator.isOnly = value;
        } else {
          iterator.isPass = value;
        }
      }
    },
    isScreens(value) {
      for (const iterator of this.arrFoldersName) {
        iterator.isScreens = value;
      }
    },
    isOnly(value) {
      if (value) {
        this.isPass = false;
      }
    },
    isPass(value) {
      if (value) {
        this.isOnly = false;
      }
    },
    folderName(newFolderName) {
      this.getFiles(newFolderName);
    }
  },
  methods: {
    clickCheckBox(index) {
      console.log(this.isOnly);
      console.log(this.isPass);
    },
    showModalHelper() {
      this.$modal.show('modal-helper');
    },
    hideModalHelper() {
      this.$modal.hide('modal-helper');
    },
    backFolders() {
      this.$router.push('/');
    },
    async createScreenshots() {
      let response, result;
      let self = this;
      try {
        response = await fetch(this.baseURL + 'create_screenshot', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify(self.arrFoldersName)
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }
    },
    async getFiles(folderName) {
      let response, result;
      // let self = this;
      try {
        response = await fetch(this.baseURL + 'screenshot', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8'
          },
          body: JSON.stringify({ name: folderName })
        });
      } catch (error) {
        console.log(error);
      }
      try {
        result = await response.json();
      } catch (error) {
        console.log(error);
      }

      this.arrFoldersName = result.sort(
        (a, b) =>
          this.getNumberFromString(a.name) - this.getNumberFromString(b.name)
      );
    },
    getNumberFromString(str) {
      let result = str.match(/^\d\d\d?/);
      return result[0];
    }
  },
  mounted() {},
  created() {}
};
</script>

 <style scoped>
.folder-wrapper {
  display: flex;
  flex-direction: column;
}

.folder-wrapper .item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0.4rem;
  margin-bottom: 1rem;
  background-color: hsl(var(--accent-color));
  border-radius: 0.2rem;
}

.folder-wrapper .item .inner {
  display: flex;
  align-items: center;
}

.folder-wrapper .item .delay {
  width: 3.5rem;
}

.options {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  align-items: center;
  margin-bottom: 1rem;
}

.switch-wrapper {
  display: flex;
  align-items: center;
}

.switch-wrapper .switch-text {
  cursor: pointer;
  margin-left: 0.5rem;
  margin-bottom: 1rem;
}

.autoFill {
  height: 1.5rem;
  display: flex;
  justify-content: flex-end;
}

.autoFill input {
  margin-right: 0.2rem;
}

.autoFill .checkbox label::before {
  top: -1.5rem;
  left: -1rem;
}

.autoFill .checkbox label::after {
  left: -0.5rem;
  top: -1rem;
}

.checkbox input[type='checkbox'] {
  display: none;
}

.checkbox label::before {
  content: '';
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  border: 1px solid;
}

.checkbox label::after {
  content: '';
  display: inline-block;
  height: 6px;
  width: 9px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
}

.checkbox label {
  position: relative;
}
.checkbox label::before,
.checkbox label::after {
  position: absolute;
}

/*Внешний блок*/
.checkbox label::before {
  top: -1.05rem;
  left: -0.6rem;
}

/*Галка*/
.checkbox label::after {
  left: -0.1rem;
  top: -0.5rem;
}

/*Прячем галку по умолчанию*/
.checkbox input[type='checkbox'] + label::after {
  content: none;
}

/*Показываем галку по состоянию checked*/
.checkbox input[type='checkbox']:checked + label::after {
  content: '';
}

/*Добавляем фокус к внешнему блоку поддельного чекбокса*/
.checkbox input[type='checkbox']:focus + label::before {
  outline: rgb(59, 153, 252) auto 5px;
}
</style>